import React, { useEffect } from "react";
import { useState } from "react";
import Circle from "../Components/Circle";
import { useDispatch } from "react-redux";
import { countuser } from "../features/UserCount/UserCount";

const Home = () => {
  const [count, setCount] = useState(0);
  const [background, setBackground] = useState("yellow");
  const [data, setData] = useState([{ count: count, background: background }]);

  const dispatch = useDispatch();

  const ChangeColor = (index) => {
    data[index] = {
      background: "#" + Math.floor(Math.random() * 0xffffff).toString(16),
      count: data[index].count + 1,
      ...data,
    };

    setCount(count + 1);
    setBackground("#" + Math.floor(Math.random() * 0xffffff).toString(16));
  };

  const Duplicate = (ind) => {
    if (data.length < 10) {
      const previous_background = data[ind].background;

      dispatch(countuser(data.length + 1));
      setData([...data, { count: 0, background: previous_background }]);
    }
  };

  useEffect(() => {
    dispatch(countuser(data.length));
  }, []);

  return (
    <>
      {data.length >= 10 && (
        <div className="alert alert-danger" role="alert">
          <strong className="text-danger">
            Can't Duplicate more than 10 circles
          </strong>
        </div>
      )}
      <div
        className="container-fluid-lg d-flex justify-content-center align-items-center  w-100 gap-md-4 gap-2 flex-wrap py-3 "
        style={{
          height: "auto",
          minHeight: "80dvh",
          width: "100vw",
          overflowWrap: "break-word",
        }}
      >
        {data &&
          data.map((item, ind) => {
            return (
              <Circle
                key={ind}
                index={ind}
                count={item.count}
                Duplicate={Duplicate}
                ChangeColor={ChangeColor}
                background={item.background}
              />
            );
          })}
      </div>
    </>
  );
};

export default Home;
