import React from "react";

const Circle = ({ index, Duplicate, ChangeColor, count, background }) => {
  return (
    <>
      <div
        className={`my-1   d-flex justify-content-center align-items-center flex-wrap}`}
        style={{
          width: "200px",
          minHeight: "200px",
          borderRadius: "50%",
          backgroundColor: background,
          boxShadow: "rgba(99, 99, 99, 0.2) 0px 2px 8px 0px",
        }}
      >
        <div className="w-100 d-flex justify-content-center align-items-center flex-column">
          <p className="text-info">{count}</p>
          <p className="text-info"> {background}</p>

          <div className="d-flex justify-content-between flex-column  gap-2">
            <button
              className="btn btn-primary flex-1"
              onClick={() => ChangeColor(index)}
            >
              Change Color
            </button>
            <button
              className="btn btn-success flex-1"
              onClick={() => Duplicate(index)}
            >
              Duplicate
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default Circle;
