import React from "react";
import Card from "../Components/Card";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";

const Users = () => {
  const [userdata, setUserData] = useState(null);
  const CountUser = useSelector((state) => state.usercountSlice.value);

  useEffect(() => {
    fetch("https://random-data-api.com/api/users/random_user?size=" + CountUser)
      .then((response) => response.json())
      .then((data) => {
        setUserData(data);
      })
      .catch((error) => {
        console.error("Error:", error);
      });
  }, []);
  return (
    <div className="container my-3 py-5" style={{ minHeight: "80dvh" }}>
      <div className="row flex-wrap">
        {userdata !== null ? (
          userdata.map((item) => {
            return (
              <Card
                FullName={item.first_name + item.last_name}
                id={item.id}
                avatar={item.avatar}
                title={item.employment.title}
                username={item.username}
              />
            );
          })
        ) : (
          <>
            <div>
              <p className="display-5 fw-bold text-uppercase">
                Fetching Users!..⚠️
                <br />
                <p className="text-danger">
                  Please Check your Network Connection💥
                </p>
              </p>
            </div>
            <div class="d-flex justify-content-center align-items-center">
              <div
                class="spinner-border text-primary spinner-border-lg"
                role="status"
              >
                <span class="visually-hidden">Loading...</span>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default Users;
