import React from "react";
import { useSelector } from "react-redux";
const UserInfo = () => {
  const userInfo = useSelector((state) => state.userSlice.value);

  return (
    <>
      <div
        className="container d-grid place-content-center;"
        style={{ minHeight: "90dvh", placeContent: "center" }}
      >
        <div className="p-5 mb-4 bg-light rounded-3">
          <div
            className="container-fluid py-5 rounded-3"
            style={{
              background:
                "linear-gradient( 45deg, hsl(229, 86%, 80%) 51%, hsl(229, 90%, 77%) 51%, hwb(229 56% 2%) 51% )",
              boxShadow: "rgba(99, 99, 99, 0.2) 0px 2px 8px 0px",
            }}
          >
            <div className="d-flex">
              <div>
                <div className="d-flex align-items-center">
                  <div className="flex-shrink-0">
                    <img src={userInfo && userInfo.Avatar} alt="" width="300" />
                  </div>
                </div>
              </div>
              <div>
                <h1 className="display-5 fw-bold">
                  FullName:&nbsp;{userInfo && userInfo.FullName}
                </h1>
                <p className=" fs-4">
                  Title:&nbsp;{userInfo && userInfo.Title}
                </p>
                <p className=" fs-4">Id:&nbsp;{userInfo && userInfo.Id}</p>
                <p className=" fs-4">
                  Username:&nbsp;{userInfo && userInfo.Username}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default UserInfo;
