import { createSlice } from "@reduxjs/toolkit";
const initialState = {
  value: 1,
};
const usercountSlice = createSlice({
  name: "noofuser",
  initialState,
  reducers: {
    countuser: (state, action) => void (state.value = action.payload),
    // Increment: (state) => void (state.value += 1),
  },
});

export const { countuser } = usercountSlice.actions;
export default usercountSlice.reducer;
