import React from "react";
import { useDispatch } from "react-redux";
import { userdetail } from "../features/UserDetails/UserDetails";
import { useNavigate } from "react-router-dom";
const Card = ({ FullName, id, avatar, title, username }) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  return (
    <>
      <div
        className="col-3 my-2"
        role="button"
        onClick={() => {
          dispatch(
            userdetail({
              FullName: FullName,
              Avatar: avatar,
              Title: title,
              Id: id,
              Username: username,
            })
          );
          navigate("/UserInfo");
        }}
      >
        <div className="card text-center">
          <img className="card-img-top" src={avatar} alt="Title" />
          <div className="card-header">Full Name:&nbsp;{FullName}</div>
          <div className="card-body">
            <h3 className="card-title">Title:&nbsp;{title}</h3>
            <p className="card-text">Id:&nbsp;{id}</p>
          </div>
          <div className="card-footer text-muted">
            Username:&nbsp;{username}
          </div>
        </div>
      </div>
    </>
  );
};

export default Card;
